@import "https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap";
body {
  color: #3c484e;
  margin: 0;
  padding: 0;
  font-family: Open Sans, sans-serif;
}

.Main {
  grid-template-columns: minmax(auto, 768px);
  justify-content: center;
  padding: 10px;
  display: grid;
}

.Items {
  grid-gap: 1.5rem;
  grid-row-gap: 1.5em;
  grid-template-columns: repeat(2, 1fr);
  display: grid;
}

.Card {
  border-radius: 5px;
  margin: 0 0 20px;
  text-decoration: none;
  animation-name: fade;
  animation-duration: 4s;
  display: block;
  box-shadow: 8px 14px 38px #272c310f, 1px 3px 8px #272c3108;
}

.Card img {
  border-radius: 5px 5px 0 0;
  width: 100%;
  height: auto;
}

.Card h2 {
  justify-content: space-between;
  padding: 5px 10px;
  font-size: 18px;
  font-weight: 300;
  display: flex;
}

@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.footer {
  background: var(--color-background-footer, #0f172a);
  color: var(--color-footer-text, #e2e8f0);
  margin-top: auto;
  padding: 2.5rem 1.5rem;
}

.footer__content {
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
}

.footer__logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.footer__tagline {
  opacity: .75;
  font-size: .9rem;
}

.footer__links {
  gap: 1.2rem;
  display: flex;
}

.footer__links a {
  color: var(--color-footer-text, #e2e8f0);
  font-size: .95rem;
  text-decoration: none;
  transition: opacity .2s;
}

.footer__links a:hover {
  opacity: .6;
}

.footer__social {
  gap: 1.2rem;
  font-size: 1.3rem;
  display: flex;
}

.footer__social a {
  color: var(--color-footer-text, #e2e8f0);
  text-decoration: none;
  transition: opacity .2s;
}

.footer__social a:hover {
  opacity: .6;
}

.footer__bottom {
  text-align: center;
  opacity: .6;
  border-top: 1px solid #ffffff14;
  margin-top: 2rem;
  padding-top: 1rem;
  font-size: .85rem;
}

@media (width >= 700px) {
  .footer__content {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
