================================ */
.footer {
  background: var(--color-background-footer, #0F172A);
  color: var(--color-footer-text, #E2E8F0);
  padding: 2.5rem 1.5rem;
  margin-top: auto;
}

.footer__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  max-width: 1100px;
  margin: 0 auto;
}

/* Branding */
.footer__logo {
  font-size: 1.5rem;
  font-weight: 700;
}

.footer__tagline {
  font-size: 0.9rem;
  opacity: 0.75;
}

/* Links */
.footer__links {
  display: flex;
  gap: 1.2rem;
}

.footer__links a {
  color: var(--color-footer-text, #E2E8F0);
  text-decoration: none;
  font-size: 0.95rem;
  transition: opacity 0.2s ease;
}

.footer__links a:hover {
  opacity: 0.6;
}

/* Social Icons */
.footer__social {
  display: flex;
  gap: 1.2rem;
  font-size: 1.3rem;
}

.footer__social a {
  text-decoration: none;
  color: var(--color-footer-text, #E2E8F0);
  transition: opacity 0.2s ease;
}

.footer__social a:hover {
  opacity: 0.6;
}

/* Bottom */
.footer__bottom {
  text-align: center;
  padding-top: 1rem;
  margin-top: 2rem;
  font-size: 0.85rem;
  opacity: 0.6;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
}

/* Responsive */
@media (min-width: 700px) {
  .footer__content {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}